import React from "react";
import { info } from "../data/info";
import { allocation } from "../data/allocation";
import { performance } from "../data/performance";
import { positions, performanceData } from "../data/positions";
import { Allocation, Position, HistoryList} from "../data/models";
import { resolve } from "path";


function getRandomDelay() {
  // return 0;
  return 500 + (Math.random() * 2000);
  // return 9999999999;
}

export function getFundInfo(): Promise<any> {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(info);
    }, getRandomDelay());
  })
}

export function getFundAllocation(): Promise<Allocation[]> {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(allocation);
    }, 3000);
  });
}

export function getPerformance(): Promise<string[]> {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(performance);
    }, 3000);
  })
}

export function getPerformanceData(): Promise<HistoryList[]> {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve(performanceData);
    }, 3000)
  })
}

export function getPositions(): Promise<HistoryList[]> {  
    return new Promise((resolve) => {
    setTimeout(() => {
      resolve(positions);
    }, 3000);
  });
}

