import React from "react";
import { count } from "console";


export default function Home() {
  return (
    <>
      <h2>Welcome to Rabson Capital</h2>

      <p>Rabson Capital (RBC) is an investment fund that uses mathematical and statistical techniques together with automated algorithms and advanced quantitative models to make investment decisions and execute trades. There is 50% human intellect and judgment involved in investment selection and related decisions. </p>
      <p>Rabson Capital operates using computer-based models, which mitigate risks and losses related to 100% human fund management. Just like any other investment fund, Rabson Capital aims to outperform the market by placing funds with liquid and publicly traded assets in a superior way. In financial terms, the goal is to generate alpha (excess return).</p>
      <p>The quantitative models used by Rabson Capital are designed to detect investment opportunities in the market. Hence, the goal of Rabson Fund developers is to create a more superior model. The complex mathematical models carry out the buy/sell decisions alongside humans.</p>
      <p>Not Man VS Machine <br /> but <br /> Man With Machine!</p>

      <img src="/finance-chart.jpg" width="100%" alt="finance chart"/>
    </>
  )
}
