 const NumberCell = (props: GridCellProps) => {
  const field = props.field || "";
  const startingValue = props.dataItem[field];
  //console.log(btcCurrentPrice);
  let finalValue = ((startingValue / 100000000)).toFixed(7);
  /* if (startingValue > 1000000000) {
    finalValue = (startingValue / 1000000000).toFixed(2) + "B";
  }
 */
  return <td>{finalValue} BTC</td>
}

const ChangeCell = (props: GridCellProps) => {
  const field = props.field || "";
  const value = props.dataItem[field];
  return (
    <td className={value > 0 ? "change-up" : "change-down"}>
      {(value / 100000000).toFixed(7)} BTC
      {/* % */}
    </td>
  );
} 

const ChangeDate = (props: GridCellProps) => {
  const field = props.field || "";
  const value = props.dataItem[field];
  var date_;
  date_ = value ? value.substring(0,10) : 'Open Trade';
  //console.log(value.substring(0,10));
  return (
    <td >
      {date_}
      {/* % */}
    </td>
  );
} 

