import React from "react";
import { HistoryList, PerformanceData } from "../data/models";
import {
  Chart,
  ChartSeries,
  ChartSeriesItem,
  ChartCategoryAxis,
  ChartCategoryAxisItem,
  ChartTitle,
} from "@progress/kendo-react-charts";

import { getPerformance, getPerformanceData, getPositions } from "../services/dataService";
import Loading from "../layout/Loading";
import { performanceData } from "../data/positions";



export default function PerformancePanel() {

  
  let TData :any[] = [];
  let BDates : string[] = [];
  let readyDates : string[] = [];
  const [data, setData] = React.useState<string[]>();
  const [dates, setDates] = React.useState<string[]>();
  React.useEffect(() => {
    // ðŸ†— Ship it

  (async () => {
    const users = await getPerformanceData();
    console.log(performanceData);
    console.log(performanceData.length);
    const balances = JSON.stringify(performanceData,['walletBalance']);
    const bDates =  JSON.stringify(performanceData,['transactTime']);
    console.log(balances);
    

    JSON.parse(
      balances,
      (key, value : string) =>
        typeof value === "number"
          ? TData.push(value/100000000) // return value * 2 for numbers
          : value, // return everything else unchanged
    );
    // { p: 10 } 
      console.log(TData);
      setData(TData.reverse());
    
      JSON.parse(bDates, (key, value) =>
      typeof value === "string"
      ? BDates.push(value)
      :value,);
      
      var currentDate : Date;
      for (var counter=0; counter<BDates.length;counter++){
        // Creating a date object
        currentDate = new Date(BDates[counter]); // yyyy-mm-dd
        // Getting short month name (e.g. "Oct")
        var month = currentDate.toLocaleString('default', { month: 'short' });
        var day = currentDate.getDate();
        console.log(month + '\n' + day);
        readyDates.push((month + '\n' + day).toString());
      }

      readyDates = readyDates.reverse();
      setDates(readyDates);
      console.log(readyDates);          
      
    
  })();

  return () => {
    
    // this now gets called when the component unmounts
  };

    
  }, []);

  return (
    <>
      {!data && <Loading />}
      <Chart style={{ opacity: data ? "1" : "0" }}>
        <ChartTitle text="Investment Performance" />
        <ChartCategoryAxis>
          <ChartCategoryAxisItem categories={dates} />
        </ChartCategoryAxis>
        <ChartSeries>
          <ChartSeriesItem type="line" data={data} />
        </ChartSeries>
      </Chart>
    </>
  )
}
